.payment {
  background-color: white;
}
.paymentContenar > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}
.paymentSection {
  display: flex;
  padding: 20px;
  margin: 20px;
  border-bottom: 1px solid lightgray;
}
.paymentTitle,
.paymentSection > h3 {
  flex: 0.2;
}
.paymentAddrress,
.paymentItims,
.paymentDetails {
  flex: 0.8;
}

.paymentDetails .paymentPrice > h3 {
  padding-top: 20px;
}

.paymentDetails > form > div > button {
  background-color: #f0c14b;
  border-radius: 5px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.paymentDetails > form > div > button:active {
  transform: scale(0.97);
}
